     'Blaming the victims': 'Blaming the victims',
     para1: 'In May 2013, the traffic court of Moabit fined a truck driver who had reversed over a 71-year-old to pay 1,200 euros. In justifying their decision, the court said that the elderly should have seen the truck coming towards him. As if the old man had willingly jumped under the lorry.',
