     'Attempted murder': 'Attempted murder',
     para4: 'A great many homicides against pedestrians or cyclists never go to court because prosecutors consider that they were unavoidable and that no one is to blame. However, Berlin prosecutors have already charged homicidal drivers with murder. In one case, <a href="https://www.bz-berlin.de/tatort/menschen-vor-gericht/verteidiger-der-kudamm-raser-plaedieren-auf-fahrlaessigkeit" target="_blank">a car crashed in another one</a> after a race on Kudamm, in the center. In another, <a href="https://www.rbb24.de/panorama/beitrag/2018/09/autoraser-berlin-urteil-versuchter-mord.html" target="_blank">a man tried to escape a police control</a> by driving at 100km/h in a side street where traffic was limited to 30. He almost killed a child and her mother. In both cases, prosecutors sought life sentences and judges mostly complied.',
     para5: 'Charging drivers with murder does not go easy in Germany. The first of the two cases went to the supreme court, where the ruling <a href="http://juris.bundesgerichtshof.de/cgi-bin/rechtsprechung/document.py?Gericht=bgh&Art=en&Datum=Aktuell&nr=81300&linked=pm" target="_blank">was nullified</a>. Driving 160km/h in the middle of Berlin does not mean that one had the intent to kill, said the court.',
     para6: 'Although car drivers, even drunk, even speeding, can only ever be charged with negligent homicide, anyone who disrupts traffic is immediately charged with attempted murder. After someone threw a stone on the highway, for instance, prosecutors <a href="https://www.morgenpost.de/berlin-aktuell/article105822215/Steinwuerfe-auf-der-A10-sind-versuchter-Mord.html" target="_blank">opened a case</a> for attempted murder. In another case, two twenty-somethings who, "out of boredom", <a href="https://www.morgenpost.de/berlin/article103220377/Zwei-Berliner-warfen-Stahlplatte-auf-Autobahn.html" target="_blank">had thrown</a> a metal chain on the highway were tried on attempted murder charges.',
     para7: 'A small, overwhelmingly male group of drivers, helped by the police and the judiciary, has the exorbitant priviledge of having the right to kill with little or no consequences. Their victims, largely non-drivers, are often blamed for trying to stay alive in front of them. As long as this culture of impunity continues, pedestrians and cyclists will keep dying and Berlin will remain <a href="https://blog.nkb.fr/danger-cyclists/" target="_blank">one of the most dangerous cities in Europe</a>.',
